import React, { useEffect, useState } from 'react'
import './LoginK.css'
import { Link } from 'react-router-dom';
import { Button, Grid } from "@mui/material";

const User = {
    email: 'test@example.com',
    pw: 'test2323@@@'
}


export default function LoginK() {
    const [email, setEmail] = useState('');
    const [pw, setPw] = useState('');

    const [emailValid, setEmailValid] = useState(false);
    const [pwValid, setPwValid] = useState(false);
    const [notAllow, setNotAllow] = useState(true);

    useEffect(() => {
        if(emailValid && pwValid) {
        setNotAllow(false);
        return;
        }
        setNotAllow(true);
    }, [emailValid, pwValid]);

    const handleEmail = (e) => {
        setEmail(e.target.value);
        const regex = 
        //eslint-disable-next-line
        /^(([^<>()\[\].,;:\s@"]+(\.[^<>()\[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/i;
        if (regex.test(e.target.value)) {
        setEmailValid(true);
        } else {
        setEmailValid(false);
        }
    };
    const handlePw = (e) => {
        setPw(e.target.value);
        const regex = /^(?=.*[a-zA-z])(?=.*[0-9])(?=.*[$`~!@$!%*#^?&\\(\\)\-_=+])(?!.*[^a-zA-z0-9$`~!@$!%*#^?&\\(\\)\-_=+]).{8,20}$/;
        if (regex.test(e.target.value)) {
        setPwValid(true);
        } else {
        setPwValid(false);
        }
    };
    const onClickConfirmButton = () => {
        if(email === User.email && pw === User.pw) {
        alert('로그인에 성공했습니다.')
        } else {
        alert("등록되지 않은 회원입니다.");
        }
    }

    return (
        <div className="page">
        <div className="titleWrap">
            엔지니어링 설계정보 디지털 변환 플랫폼
        </div>

        <div className='login_content'>
            <div>
                <img src="/img/network.png" alt='img' className='networkImg' />
            </div>

            <div className="login_border"></div>

            <div className="contentWrap">
                <div className="inputTitle">이메일</div>
                <div
                className="inputWrap">
                <input
                    className="input"
                    type="text"
                    placeholder="test@gmail.com"
                    value={email}
                    onChange={handleEmail}
                />
                </div>
                <div className="errorMessageWrap">
                {!emailValid && email.length > 0 && (
                    <div>올바른 이메일을 입력해주세요.</div>
                )}
                </div>

                <div style={{ marginTop: "26px" }} className="inputTitle">
                비밀번호
                </div>
                <div className="inputWrap">
                <input
                    className="input"
                    type="password"
                    placeholder="영문, 숫자, 특수문자 포함 8자 이상"
                    value={pw}
                    onChange={handlePw}
                />
                </div>
                <div className="errorMessageWrap">
                {!pwValid && pw.length > 0 && (
                    <div>영문, 숫자, 특수문자 포함 8자 이상 입력해주세요.</div>
                )}
                </div>

                <div className='find'>
                    <Link style={{color: "black"}}> 회원정보를 잊어버렸나요?</Link>
                </div>
                
                <div className='buttons'>
                    <div>
                        <Button
                            onClick={onClickConfirmButton} 
                            disabled={notAllow} 
                            className="loginButton"
                            type="submit" 
                            variant="contained"
                            sx={{
                                backgroundColor:'#8cd8e9', 
                                height: '4.5vh', 
                                width: '42vh', 
                                borderRadius:30,
                                fontSize: 24,
                                fontWeight: 600,
                                '&:hover': {backgroundColor: '#6ba3af'}}}>
                        로 그 인
                        </Button>
                    </div>

                    <div>
                        <Link to="/Example" style={{ color:'white', textDecoration: 'none' }}>
                            <Button
                                type="submit" 
                                variant="contained" 
                                sx={{
                                    backgroundColor:'#a9d18e', 
                                    height: '4.5vh', 
                                    width: '42vh',
                                    borderRadius:30,
                                    fontSize: 24,
                                    fontWeight: '600',
                                    '&:hover': {backgroundColor: '#7c9a67'},
                                    mt: '1.7vh'
                                }}>
                                회 원 가 입
                            </Button>
                        </Link>
                    </div>
                </div>    
            </div>
        </div>    
    </div>
    );
}